export default {
  props: {
    item: {
      type: Object,
      default: () => {
        return {}
      }
    },
    start: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      userArray: []
    }
  },
  mounted () {
    this.userArray = this.array2double(this.item.USERARRAY)
  },
  methods: {
    array2double (tmpArray) {
      // 获取数组的长度
      let len = tmpArray.length
      let n = 8 // 假设每行显示7个
      let res = []
      if (len) {
        let lineNum = len % n === 0 ? len / n : Math.floor((len / n) + 1)
        for (let i = 0; i < lineNum; i++) {
          let temp = tmpArray.slice(i * n, i * n + n)
          res.push(temp)
        }
      }
      return res
    }
  }
}
