export default {
  props: {
    score: {
      type: Number,
      default: 0
    },
    index: {
      type: Number,
      default: 0
    },
    text: {
      type: String,
      default: ''
    }
  },
  data () {
    return {
      start: false
    }
  },
  computed: {
    width () {
      const splitPoint = 0
      const max = 5.5
      const minWidth = 25
      if (this.score <= splitPoint) {
        return minWidth
      } else {
        const v = (this.score - splitPoint) / (max - splitPoint) * 100
        return v > minWidth ? v : minWidth
      }
    }
  },
  mounted () {
    setTimeout(() => {
      this.start = true
    }, 50)
  }
}
